<template>
  <div class="header">
    <!-- 折叠按钮 -->
    <div class="collapse-btn" @click="collapseChage">
      <i class="el-icon-menu"></i>
    </div>
    <div class="logo">后台管理系统</div>
    <div class="header-right">
      <div class="header-user-con">
        <!-- 全屏显示 -->
        <div class="btn-fullscreen" @click="handleFullScreen">
          <el-tooltip effect="dark" :content="fullscreen?`取消全屏`:`全屏`" placement="bottom">
            <i class="el-icon-rank"></i>
          </el-tooltip>
        </div>
        <!-- 消息中心 -->
       <!-- <div class="btn-bell">
          <el-tooltip effect="dark" :content="message?`有${message}条未读消息`:`消息中心`" placement="bottom">
            <router-link to="/tabs">
              <i class="el-icon-bell"></i>
            </router-link>
          </el-tooltip>
          <span class="btn-bell-badge" v-if="message"></span>
        </div>-->
        <!-- 用户头像 -->
        <div class="user-avator"><img src="../../../static/img/imgHeard.png"></div>
<!--         用户名下拉菜单-->
        <el-dropdown class="user-name" trigger="click" @command="handleCommand">
                    <span class="el-dropdown-link">
                        {{username}} <i class="el-icon-caret-bottom"></i>
                    </span>
          <el-dropdown-menu slot="dropdown">
<!--            <a href="javascript:;" @click="dialogVisible = true">-->
<!--              <el-dropdown-item>修改密码</el-dropdown-item>-->
<!--            </a>-->
            <el-dropdown-item divided command="loginout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-dialog
          title="重置密码"
          :visible.sync="dialogVisible"
          width="40%"
          :before-close="handleClose">
          <el-form :model="form">
            <el-form-item label="原密码" :label-width="formLabelWidth">
              <el-input type="password" v-model="form.old_passwd" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="新密码" :label-width="formLabelWidth">
              <el-input type="password" v-model="form.new_passwd" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="确认新密码" :label-width="formLabelWidth">
              <el-input v-model="form.twoPassword" type="password" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取消</el-button>
            <el-button type="primary" @click="changePwd('form')">确定</el-button>
          </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>
<script>
  import http from '../../assets/js/http';
  import bus from './bus';
  import md5 from 'js-md5';
  // import * as types from '../../vuex/types.js'
  // import {mapState} from 'vuex';

  export default {
    data() {
      return {
        collapse: false,
        fullscreen: false,
        name: 'linxin',
        message: 2,
        username: '',
        dialogVisible: false,
        form:{
          aid:'',
          old_passwd:'',
          new_passwd:'',
          twoPassword:''
        },
        new_pwd2:'',
        formLabelWidth:'100px'
      }
    },
    created() {
      // this.username = JSON.parse(localStorage.getItem('ms_username')).name
    },
    methods: {
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {
          });
      },
      // 用户名下拉菜单选择事件
      //修改密码
      changePwd(f){
        let that = this;
        var newP = md5(that.form.new_passwd),
          oldP = md5(that.form.old_passwd);
        if (that.Empty(that.form.new_passwd, '请输入新密码') && that.Empty(that.form.twoPassword, '请确认新密码') && that.Empty(that.form.old_passwd, '请输入原密码')) {
          if (that.form.new_passwd != that.form.twoPassword) {
            this.$alert('请输入一致的新密码');
            return;
          }
          var params = {
            // aid:JSON.parse(localStorage.getItem('ms_username')).id,
            old_passwd:oldP,
            new_passwd:newP
          };
          this.apiPost('/sys/'+window.mid+'/admin/rbac.v1.users.changePasswd',params).then(res => {
            this.handelResponse(res,(data)=>{
              this.$message.success('修改成功');
              this.$router.push('/login')
            })
          })
        }
      },
      //退出登录
      handleCommand(command) {
        let that = this;
        if (command == 'loginout') {
          this.apiPost('/admin/logout').then(res => {
            this.handelResponse(res,(data)=>{
              this.$router.push('/login');
              // localStorage.setItem('token','');
            })
          })
        }
      },
      // 侧边栏折叠
      collapseChage() {
        this.collapse = !this.collapse;
        bus.$emit('collapse', this.collapse);
      },
      // 全屏事件
      handleFullScreen() {
        let element = document.documentElement;
        if (this.fullscreen) {
          if (document.exitFullscreen) {
            document.exitFullscreen();
          } else if (document.webkitCancelFullScreen) {
            document.webkitCancelFullScreen();
          } else if (document.mozCancelFullScreen) {
            document.mozCancelFullScreen();
          } else if (document.msExitFullscreen) {
            document.msExitFullscreen();
          }
        } else {
          if (element.requestFullscreen) {
            element.requestFullscreen();
          } else if (element.webkitRequestFullScreen) {
            element.webkitRequestFullScreen();
          } else if (element.mozRequestFullScreen) {
            element.mozRequestFullScreen();
          } else if (element.msRequestFullscreen) {
            // IE11
            element.msRequestFullscreen();
          }
        }
        this.fullscreen = !this.fullscreen;
      }
    },
    mounted() {
      if (document.body.clientWidth < 1500) {
        this.collapseChage();
      }
    },
    mixins:[http]
  }
</script>
<style scoped>
  .header {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 70px;
    font-size: 22px;
    color: #fff;
  }
  .collapse-btn {
    float: left;
    padding: 0 21px;
    cursor: pointer;
    line-height: 70px;
  }
  .header .logo {
    float: left;
    width: 250px;
    line-height: 70px;
  }
  .header-right {
    float: right;
    padding-right: 50px;
  }
  .header-user-con {
    display: flex;
    height: 70px;
    align-items: center;
  }
  .btn-fullscreen {
    transform: rotate(45deg);
    margin-right: 5px;
    font-size: 24px;
  }
  .btn-bell, .btn-fullscreen {
    position: relative;
    width: 30px;
    height: 30px;
    text-align: center;
    border-radius: 15px;
    cursor: pointer;
  }
  .btn-bell-badge {
    position: absolute;
    right: 0;
    top: -2px;
    width: 8px;
    height: 8px;
    border-radius: 4px;
    background: #f56c6c;
    color: #fff;
  }
  .btn-bell .el-icon-bell {
    color: #fff;
  }
  .user-name {
    margin-left: 10px;
  }
  .user-avator {
    margin-left: 20px;
  }
  .user-avator img {
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
  .el-dropdown-link {
    color: #fff;
    cursor: pointer;
  }
  .el-dropdown-menu__item {
    text-align: center;
  }
</style>



