<template>
    <div class="sidebar">
        <el-menu class="sidebar-el-menu" :default-active="onRoutes" :collapse="collapse"  background-color="#324157"
            text-color="#bfcbd9" active-text-color="#20a0ff" unique-opened router>
            <template v-for="item in items">
                <template v-if="item.subs">
                    <el-submenu :index="item.title" :key="item.url">
                        <template slot="title">
                            <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
                        </template>
                        <template v-for="subItem in item.subs">
                            <el-submenu v-if="subItem.subs" :key="subItem.url">
                                <template slot="title">{{ subItem.title }}</template>
                                <el-menu-item v-for="(threeItem,i) in subItem.subs" :key="i" :index="threeItem.url">
                                    {{ threeItem.title }}
                                </el-menu-item>
                            </el-submenu>
                            <el-menu-item v-else :index="subItem.url" :key="subItem.url">
                                {{ subItem.title }}
                            </el-menu-item>
                        </template>
                    </el-submenu>
                </template>
                <template v-else>
                    <el-menu-item :index="item.url" :key="item.id">
                        <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
                    </el-menu-item>
                </template>
            </template>
        </el-menu>
    </div>
</template>

<script>
    import bus from './bus';
    export default {
        data() {
            return {
                collapse: false,
                items: [
                  {
                    icon: 'el-icon-s-home',
                    url: 'banner',
                    title: 'banner图'
                  },
                  {
                    icon:'el-icon-s-management',
                    index:1,
                    title: '体检服务',
                    subs:[{
                      url:'geren',
                      title:'个人体检'
                    },{
                      url:'team',
                      title:'团队体检'
                    },{
                      url:'ruzhi',
                      title:'入职体检'
                    }]
                  },
                  {
                    icon: 'el-icon-c-scale-to-original',
                    url: 'qyinfo',
                    title: '企业信息'
                  },
                  {
                    icon: 'el-icon-s-grid',
                    url: 'recruit',
                    title: '招聘信息'
                  },
                  {
                    icon: 'el-icon-document-copy',
                    url: 'article',
                    title: '文章'
                  },

                  // {
                  //   icon: 'el-icon-lx-apps',
                  //   url: 'price',
                  //   title: '询价管理'
                  // },
                  // {
                  //   icon: 'el-icon-s-claim',
                  //   url: 'release',
                  //   title: '发布管理'
                  // },
                  // {
                  //   icon: 'el-icon-message-solid',
                  //   url: 'msgS',
                  //   title: '消息列表'
                  // },

                  // {
                  //   icon: 'el-icon-lx-cascades',
                  //   url: 'bannar',
                  //   title: '轮播图'
                  // },
                ]
            }
        },
        computed:{
            onRoutes(){
              return this.$route.path.replace('/','');
            }
        },
        created(){
          // console.log(localStorage.getItem('menusList'))

          // this.items = JSON.parse(localStorage.getItem('menusList'));
            // 通过 Event Bus 进行组件间通信，来折叠侧边栏
            bus.$on('collapse', msg => {
                this.collapse = msg;
            })
        }
    }
</script>

<style scoped>
    .sidebar{
        display: block;
        position: absolute;
        left: 0;
        top: 70px;
        bottom:0 ;
        overflow-y: scroll;
    }
    .sidebar::-webkit-scrollbar{
        width: 0;
    }
    .sidebar-el-menu:not(.el-menu--collapse){
        width: 250px;
    }
    .sidebar > ul {
        height:100%;
    }
</style>



